html {
  background-color: white;
  font-family: "Noto Sans JP", "Helvetica Neue", "Helvetica", "Hiragino Sans", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
  font-size: 16px;
}

body {
  margin: 0;
  height: 100%;
}

h2 {
  font-size: 24px;
}

.contentWrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.main {
  width: 80%;
  margin: auto;
}

.header {
  width: 100%;
  box-sizing: border-box;

  margin-bottom: 20px;
  padding: 5px 0;

  box-shadow: 0 3px 10px #00000040;

}

.header .headerBox {
  width: 80%;
  margin: 5px auto;

  display: flex;
  align-items: center;

  justify-content: left;
}

.header .logo {
  height: 40px;
}

.header .logoImg {
  height: 40px;
}

.title {
  font-size: 20px;
  font-weight: bold;
  margin-left: 15px;
}

.footer {
  width: 100%;
  flex-grow: 1;
  overflow: hidden;
  box-sizing: border-box;

  margin-top: 50px;
  padding: 30px 0;

  box-shadow: 0 3px 10px #00000040 inset;

  text-align: center;
}

.charaList {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.charaList .charaLink {
  height: 60px;
  width: 60px;

  display: inline-flex;
  justify-content: center;
  align-items: center;

  margin: 5px;
  border: 2px solid #c0c0c0;
  font-size: 25px;
}

.charaList .charaLink:hover {
  background-color: #d5f0eb;
}

.charaList > a {
  color: initial;
  text-decoration: none;
}

.pageWrapper {
  display: flex;
  flex-flow: row;
}

.info {
  display: flex;
  flex-flow: column;
  width: 25%;
  margin: 0 20px;
}

.info > .charaFrame {
  width: 100%;
  height: 150px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.info > .charaFrame > .mainChara {
  font-size: 100px;
}

.phun {
  font-family: "Phun-Sans", "PhunWrite", "Times New Roman", "YuMincho", "Hiragino Mincho ProN", "Yu Mincho", "MS PMincho", serif;
  font-weight: normal;
}

.dataTable {
  border-collapse: collapse;
  border-spacing: 0;
}

.dataTable * {
  border: 1px solid #a0a0a0;
  padding: 5px;
}

.dataTable th {
  background-color: #f5f5f5;
  width: 30%;
}

.langs {
  flex-grow: 1;
}

.langs > .langData > h2 {
  border-left: 5px solid #014f41;
  padding: 2px 0 2px 0.5em;
  margin: 30px 0 15px 0;
  
  font-size: 1.5rem;
}

.langs .langData .dataRow {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #a0a0a0;
  padding: 0;
}

.langs .langData .dataRow .dataLabel {
  font-weight: bold;
  background-color: #dcf2ee;
  width: 5rem;
  padding: 5px 0 5px 7px;
  margin-right: 10px;
}

.langs > .langData > .dataEntry > p {
  margin: 0;
}

.phun404 {
  font-size: 24px;
}

@media screen and (max-width: 750px) {
  .App {
    width: 90%;
  }
  .pageWrapper {
    flex-flow: column;
  }
  .info {
    width: 100%;
    margin: 0;
  }
}

@font-face {
  font-family: "PhunWrite";
  src: url(https://kaeru2193.github.io/Phun-Resources/font/PhunWrite-Regular-latest.woff) format("woff");
}

@font-face {
  font-family: "Phun-Sans";
  src: url(https://kaeru2193.github.io/Phun-Resources/font/Phun-Sans/Phun-Sans-Rounded-Regular.woff) format("woff");
  font-weight: normal;
}

@font-face {
  font-family: "Phun-Sans";
  src: url(https://kaeru2193.github.io/Phun-Resources/font/Phun-Sans/Phun-Sans-Rounded-Bold.woff) format("woff");
  font-weight: bold;
}